import React from 'react';
import { PageConfig } from '../../types';
import { AutoPatternsCollectionPage } from '../AutoPatternsCollectionPage';
import { AutoPatternsEntityPage } from '../AutoPatternsEntityPage';
import { SchemaProvider } from '../../providers/SchemaContext';
import { SkeletonEntity } from '../AutoPatternsEntityPage/SkeletonEntity';

export interface AutoPatternsPageProps {
  pageConfig: PageConfig;
}

export const AutoPatternsPage = ({ pageConfig }: AutoPatternsPageProps) => {
  const { type, collectionPage, entityPage } = pageConfig;

  switch (type) {
    case 'collectionPage':
      if (collectionPage) {
        return <AutoPatternsCollectionPage configuration={collectionPage} />;
      }
      break;
    case 'entityPage':
      if (entityPage) {
        return (
          <SchemaProvider
            collection={entityPage}
            skeleton={<SkeletonEntity entityPage={entityPage} />}
          >
            <AutoPatternsEntityPage configuration={entityPage} />
          </SchemaProvider>
        );
      }
      break;
    default:
      break;
  }

  return null;
};
